
.starlight-button {
    height: 50px;
    background: rgb(231, 236, 242);
    color: black;
    box-shadow: 0px 0px 36px 12px #277edd;
    border-radius: 10px;
    border: 0px;
    font-family: starWarsFont;
    font-size: 24px;
    font-weight: bold;
    outline: none
}

.starlight-button:hover {
    background: rgb(253, 238, 231);
    box-shadow: 00px 0px 36px 12px #ef3125;
    font-family: starWarsJholFont;
    cursor: pointer;
}

@media (max-width: 550px) {
  .starlight-button {
      font-size: 16px;
      height: 40px;
  }
}

@media (max-width: 400px) {
    .starlight-button {
        font-size: 12px;
        height: 30px;
    }
  }